import React from 'react';
import axios from 'axios';
import cookie from 'react-cookie';

class SearchContainer extends React.Component {
  constructor(props){
    super(props);

    this.token = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..1fnUMb27ilrQFNt9Uyj1SW2P7bp2lyDfXWn_6D-tGs4";
    this.state = {
      input: "",
      data: [],
      displayData: [],
    }
    this.parseDataForInput = this.parseDataForInput.bind(this);
    this.updateInput = this.updateInput.bind(this);
  }

  // NOTE:
  /*
  We have added my personal token below for demonstration purposes, otherwise
  we would use react-cookie to access the user's token with: cookie.load('connect.sid');
  */
  componentDidMount() {
    if (this.token == "") {
      this.token = cookie.load('connect.sid');
    }

    axios({
      method: "post",
      url: 'https://publist.ai/api/v2/jobs.frontend',
      headers: {
          Authorization : `Bearer-${this.token}`,
          "Content-type" : "application/json"
        },
      data: {
          "query": "hi"
        },
      }
    ).then((response) => {
      this.setState({
        data: response.data.data
      })
    });
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    console.log(this.state);
    if (
      prevState.data.length != this.state.data.length
      && this.state.data.length > 0
    ){
        this.parseDataForInput();
    }
  }

  parseDataForInput () {
    let returnData = [];
    if (this.state.input.length > 3) {
      this.data.forEach((article) => {
        if (
          article.title.includes(this.state.input) ||
          article.description.includes(this.state.input)){
          returnData.push(createDataNode(article));
        }
      });

      this.setState({displayData: returnData});
    }
  }

  createDataNode(article) {
    return(
      <div>
        <div>{article.title}</div>
      </div>
    )
  }

  updateInput(e) {
    this.setState({input: e.target.value})
  }

  render() {
    return(
      <main className="search-container">
        <style jsx>{`
          .search-container {
             width: 100%;
             margin: auto;
          }
          .inner-search-container {
            position: relative;
            margin: 50px;
            display: flex;
            flex-direction: column;
            background: white;
            border-radius: 6px;
            min-height: 100vh;
            box-shadow: 0 2px 8px rgba(0,0,0,.2);
          }
          .search-title{
            position: absolute;
            display: flex;
            justify-content: center;
            font-size: 34px;
            width: 100%;
            color: rgb(163,163,163);
            opacity: .5;
          }
          .search-input {
            font-size: 34px;
            border: 0px;
            margin-bottom: 4px;
            margin-top: 25px;
            width: 100%;
            max-width: 300px;
          }
          .search-input:hover {
            border-bottom: 1px solid black;
          }
          .search-input:focus {
            outline: none;
            border-bottom: 1px solid black;
          }
        `}</style>
        <section className="inner-search-container">
          <div className="search-title">
            <input className="search-input"placeholder="Search..." onChange={this.updateInput}></input>
          </div>
          <div>
            {this.state.displayData}
          </div>
        </section>
      </main>
    )
  }
}

export default SearchContainer;



